import React from 'react';
import moment from 'moment';
import {SingleDatePicker} from 'react-dates';
import 'react-dates/initialize';
import 'react-dates/lib/css/_datepicker.css';


export default class ExpenseForm extends React.Component{
  constructor(props) {
    super(props);
    console.log(props);
    this.state = {
      description:props.expense && props.expense.description || '',
      note:props.expense && props.expense.note ||'',
      amount:props.expense && props.expense.amount.toString() ||'',
      createdAt:props.expense && new moment(props.expense.createdAt) || new moment(),
      calendarFocused: false,
      error: undefined
    };
  }
  onDescriptionChange = (e)=>{
    const description = e.target.value;
    this.setState(()=>({description}));
  };
  onNoteChange = (e)=>{
    const note = e.target.value;
    this.setState(()=>({note}));
  };
  onAmountChange = (e)=>{
    const amount = e.target.value;
    if(!amount ||  amount.match(/^\d{1,}(\.\d{0,2})?$/)){
      this.setState(()=>({amount }));

    }
  };
  onDateChange = (createdAt)=>{

    if(createdAt){
      this.setState(()=>({createdAt}));
    }
      };
  onFocusChange = ({focused:calendarFocused})=>{
    this.setState(()=>({calendarFocused}));
  };
  onSubmit = (e)=>{
    e.preventDefault();

    if(this.state.description && this.state.amount){
      if(this.state.error)this.setState(()=>({error:undefined}));

      const currentExpense = {
        description: this.state.description,
        amount: parseFloat(this.state.amount,10),
        createdAt: this.state.createdAt.valueOf(),
        note: this.state.note
      };
      this.props.onSubmit(currentExpense);
    }else{
      this.setState(()=>({error: 'Not everything is defined'}));
    }

  };

  render(){
    return (
      <div>
        {this.state.error ? <h1>{this.state.error}</h1>: undefined}
        <form onSubmit = {this.onSubmit}>
          <input type = "text"
             placeholder = "Description" autoFocus
            value = {this.state.description}
            onChange = {this.onDescriptionChange} />
          <input type = "text" placeholder = "Amount"
            value = {this.state.amount}
            onChange = {this.onAmountChange} />
          <SingleDatePicker date={this.state.createdAt}
              onDateChange={this.onDateChange}
              focused = {this.state.calendarFocused}
              onFocusChange={this.onFocusChange}
              numberOfMonths={1}
              isOutsideRange={()=>false} />
          <textarea placeholder = "Your note for an expense (optional)"
            value = {this.state.note}
            onChange = {this.onNoteChange}>
          </textarea>
          <button>Add expense</button>
        </form>
      </div>
    );
  }
}
